import React, { useState, useEffect, useRef } from 'react';
import './animate.min.css';
import './textLoopAnimation.css';

const EmailFadeUpLoop = () => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const [isFadingOut, setIsFadingOut] = useState(false);

    //   
    const generateEmails = () => {
        const domains = ['gmail.com', 'yahoo.com', 'outlook.com', 'protonmail.com', 'icloud.com', 'mail.ru', 'yandex.ru'];
        const names = [
            'alex.johnson', 'sarah.wilson', 'mike.brown', 'emma.davis', 'david.smith',
            'olivia.miller', 'james.taylor', 'sophia.anderson', 'robert.clark', 'lisa.wright',
            'daniel.martinez', 'natalie.robinson', 'kevin.lewis', 'amanda.hernandez', 'ryan.king',
            'jennifer.scott', 'thomas.young', 'victoria.hall', 'william.allen', 'melissa.adams'
        ];

        return names.map(name => {
            const domain = domains[Math.floor(Math.random() * domains.length)];
            const email = `${name}@${domain}`;
            const firstLetter = name.charAt(0).toUpperCase();
            return { email, firstLetter };
        });
    };

    const [emails, setEmails] = useState(generateEmails());

    useEffect(() => {
        let interval;

        const startAnimationCycle = () => {
            //   
            setIsFadingOut(true);

            //    (1 )    
            setTimeout(() => {
                setCurrentIndex(prevIndex =>
                    prevIndex === emails.length - 1 ? 0 : prevIndex + 1
                );
                setIsFadingOut(false);
            }, 1000); //   fadeOutUp
        };

        interval = setInterval(startAnimationCycle, 3000); //    3 

        return () => {
            if (interval) clearInterval(interval);
        };
    }, [emails.length]);

    //    
    useEffect(() => {
        setEmails(generateEmails());
    }, []);

    return (
        <div className="email-fadeup-container">
            <div
                key={currentIndex}
                className={`email-item animate__animated ${
                    isFadingOut ? 'animate__fadeOutUp' : 'animate__fadeInUp'
                }`}
            >
                <div className="email-avatar">
                    <div className="avatar-circle">
                        {emails[currentIndex]?.firstLetter || 'U'}
                    </div>
                </div>
                <div className="email-content">
          <span className="email-address">
            {emails[currentIndex]?.email || 'user@example.com'}
          </span>
                </div>
            </div>
        </div>
    );
};

export default EmailFadeUpLoop;