import React from 'react';
import ProgressBar from "../components/progressBar";
import TextFadeUpLoop from "../components/textLoopAnimation.js";
import { DotLottieReact } from '@lottiefiles/dotlottie-react';

const HandScanScreen = ({ onNext,onBack,currentProgress,totalScreens,onUploadPhoto }) => {
    return (
        <div className="App">

            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text">{currentProgress}/{totalScreens}</div>
            </header>
            <ProgressBar currentStep={currentProgress} totalSteps={totalScreens} />


            <main>
                <h2 style={{fontFamily: 'Philosopher'}}>Take a photo of your left palm</h2>

                <div className="scan-anim-container animate__animated animate__fadeIn" style={{animationDelay: '2s'}}>
                    <div className="scan-anim">
                        <DotLottieReact
                            src="/assets/img/scanafter.lottie"
                            loop
                            autoplay
                        />
                    </div>

                    <img className="scan-anim-img" src="/assets/img/scanhand.svg" alt=""/>
                </div>
                <p className="sub-heading">Privacy is a priority for us. We only process non-identifiable data to ensure anonymity</p>

            <TextFadeUpLoop />
            </main>
            <div className="button-container mt-0 pd-0 upload-button-container">
                <button className="next-btn" onClick={onUploadPhoto}>
                    Take a photo
                </button>
                {/*<a href="#" className="sub-button" onClick={onUploadPhoto}>Upload palm photo</a>*/}

            </div>
        </div>

    );
};

export default HandScanScreen;