import React, { useState } from 'react';
import ProgressBar from "../components/progressBar";

const RelationshipScreen = ({ onNext, onBack, currentProgress, totalScreens, updateUserData }) => {
    const [selectedStatus, setSelectedStatus] = useState(null);

    const relationshipOptions = [
        { id: 1, emoji: '', text: 'In a relationship', value: 'in_relationship' },
        { id: 2, emoji: '', text: 'Just broke up', value: 'just_broke_up' },
        { id: 3, emoji: '', text: 'Engaged', value: 'engaged' },
        { id: 4, emoji: '', text: 'Married', value: 'married' },
        { id: 5, emoji: '', text: 'Looking for a soulmate', value: 'looking_for_soulmate' },
        { id: 6, emoji: '', text: 'Single', value: 'single' },
        { id: 7, emoji: '', text: 'It`s complicated', value: 'complicated' }
    ];

    const handleStatusSelect = (option) => {
        console.log('Relationship status selected:', option.text);
        setSelectedStatus(option.value);

        //   userData
        if (updateUserData && typeof updateUserData === 'function') {
            updateUserData({
                relationshipStatusText: option.text,
            });
            console.log('Relationship status saved to userData');
        }

        //    0.5 
        setTimeout(() => {
            if (onNext && typeof onNext === 'function') {
                onNext();
            }
        }, 500);
    };

    return (
        <div className="App animate__animated animate__fadeIn">
            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text">{currentProgress}/{totalScreens}</div>
            </header>

            <ProgressBar currentStep={currentProgress} totalSteps={totalScreens} />

            <main>
                <p className="title font-size-24 philosopher-bold">
                    To get started, tell us about your current <span className="blue-text philosopher-bold">relationship status</span>
                </p>

                <div className="card-container">
                    {relationshipOptions.map((option) => (
                        <div
                            key={option.id}
                            className={`card-content ${selectedStatus === option.value ? 'selected' : ''}`}
                            onClick={() => handleStatusSelect(option)}
                            style={{
                                cursor: 'pointer',
                            }}
                        >
                            <div className="card-logo">{option.emoji}</div>
                            <div className="card-text philosopher-bold">{option.text}</div>
                        </div>
                    ))}
                </div>

            </main>
        </div>
    );
};

export default RelationshipScreen;