// utils/zodiacUtils.js

//         
export const getZodiacSign = (month, day) => {
    const monthIndex = getMonthIndex(month);
    const dayNum = parseInt(day);

    //   
    if ((monthIndex === 2 && dayNum >= 21) || (monthIndex === 3 && dayNum <= 19)) return 'Aries';
    if ((monthIndex === 3 && dayNum >= 20) || (monthIndex === 4 && dayNum <= 20)) return 'Taurus';
    if ((monthIndex === 4 && dayNum >= 21) || (monthIndex === 5 && dayNum <= 20)) return 'Gemini';
    if ((monthIndex === 5 && dayNum >= 21) || (monthIndex === 6 && dayNum <= 22)) return 'Cancer';
    if ((monthIndex === 6 && dayNum >= 23) || (monthIndex === 7 && dayNum <= 22)) return 'Leo';
    if ((monthIndex === 7 && dayNum >= 23) || (monthIndex === 8 && dayNum <= 22)) return 'Virgo';
    if ((monthIndex === 8 && dayNum >= 23) || (monthIndex === 9 && dayNum <= 22)) return 'Libra';
    if ((monthIndex === 9 && dayNum >= 23) || (monthIndex === 10 && dayNum <= 21)) return 'Scorpio';
    if ((monthIndex === 10 && dayNum >= 22) || (monthIndex === 11 && dayNum <= 21)) return 'Sagittarius';
    if ((monthIndex === 11 && dayNum >= 22) || (monthIndex === 0 && dayNum <= 19)) return 'Capricorn';
    if ((monthIndex === 0 && dayNum >= 20) || (monthIndex === 1 && dayNum <= 18)) return 'Aquarius';
    if ((monthIndex === 1 && dayNum >= 19) || (monthIndex === 2 && dayNum <= 20)) return 'Pisces';

    return 'Aries'; //  
};

//      
const getMonthIndex = (monthName) => {
    const months = ['January', 'February', 'March', 'April', 'May', 'June',
        'July', 'August', 'September', 'October', 'November', 'December'];
    return months.indexOf(monthName);
};

