//       ( )
export const getAstroData = (birthdayString) => {
    if (!birthdayString) {
        return {
            moonSign: 'Cancer',
            sunSign: 'Aquarius',
            ascendant: 'Libra'
        };
    }

    //     "April 1, 2000"
    const [month, day] = birthdayString.split(' ');
    const dayNum = parseInt(day.replace(',', ''));

    //    ()
    const sunSign = getZodiacSign(month, dayNum);

    //       
    const zodiacSigns = [
        'Aries', 'Taurus', 'Gemini', 'Cancer', 'Leo', 'Virgo',
        'Libra', 'Scorpio', 'Sagittarius', 'Capricorn', 'Aquarius', 'Pisces'
    ];

    const sunIndex = zodiacSigns.indexOf(sunSign);
    const moonIndex = (sunIndex + 4) % 12;
    const ascendantIndex = (sunIndex + 7) % 12;

    return {
        moonSign: zodiacSigns[moonIndex],
        sunSign: sunSign,
        ascendant: zodiacSigns[ascendantIndex]
    };
};

//     
export const formatBirthDate = (birthdayString) => {
    if (!birthdayString) return '19.09.2000';

    try {
        const [month, day, year] = birthdayString.replace(',', '').split(' ');
        const monthIndex = getMonthIndex(month);
        const monthNum = (monthIndex + 1).toString().padStart(2, '0');
        const dayNum = day.padStart(2, '0');
        return `${dayNum}.${monthNum}.${year}`;
    } catch (error) {
        return '19.09.2000';
    }
};

