//        Date
export const getBirthDateObject = (birthdayString) => {
    if (!birthdayString) return new Date('2000-09-19');

    try {
        return new Date(birthdayString);
    } catch (error) {
        return new Date('2000-09-19');
    }
};

//    
export const getAge = (birthdayString) => {
    if (!birthdayString) return 24;

    try {
        const birthDate = new Date(birthdayString);
        const today = new Date();
        let age = today.getFullYear() - birthDate.getFullYear();
        const monthDiff = today.getMonth() - birthDate.getMonth();

        if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birthDate.getDate())) {
            age--;
        }

        return age;
    } catch (error) {
        return 24;
    }
};