import React, {useState, useCallback, useEffect} from 'react';
import './components/animate.min.css';
import './App.css';

import HomeScreen from './screens/homescreen.js';
import GenderSelectScreen from './screens/genderSelect.js';
import BirthdayScreen from './screens/birthday.js';
import TimePickerScreen from './screens/birthtime.js';
import CityScreen from './screens/city.js';
import AstroMapScreen from './screens/astromap.js';
import ForecastScreen from './screens/forecast.js';
import RelationshipScreen from './screens/relationship.js';
import GoalsScreen from './screens/goals.js';
import ColorsScreen from './screens/colors.js';
import NaturesElementsScreen from './screens/naturesElements.js';
import PeopleCardScreen from './screens/peopleCard.js';
import ForecastSecondScreen from './screens/forecastSecond.js';
import ForecastThirdScreen from './screens/forecastThird';
import HandScanScreen from './screens/handscan.js';
import PhotoUploadScreen from './screens/photoUpload.js';
import LoginScreen from './screens/login.js';
import MoneyScreen from './screens/money.js';
import BillingScreen from './screens/billing.js';
import TermsOfUseScreen from './screens/termsofuse';
import PrivacyScreen from './screens/privacy.js';
import ReportScreen from './screens/astroreport.js';



function App() {
    const [currentScreen, setCurrentScreen] = useState('home');
    const [userData, setUserData] = useState({
        gender: '',
        birthday: null,
        knowsBirthTime: true
    });

    //  userData   
    useEffect(() => {
        console.log(' Current userData:', userData);
    }, [userData]);

    //   
    const updateUserData = useCallback((updates) => {
        console.log(' Updating with:', updates);
        setUserData(prev => {
            const newData = { ...prev, ...updates };
            return newData;
        });
    }, []);

    const handleGenderSelect = (gender) => {
        updateUserData({ gender });
    };

    const navigateTo = (screen) => {
        console.log('Navigating to:', screen);
        setCurrentScreen(screen);
    };



    const goBack = () => {
        const screenOrder = [
            'home',
            'gender',
            'birthday',
            'birthtime',
            'city',
            'astromap',
            'forecast',
            'relationship',
            'goals',
            'colors',
            'naturesElements',
            'peopleCard',
            'forecastSecond',
            'handscan',
            'photoUpload',
            'forecastThird',
            'login'
        ]; // 
        const currentIndex = screenOrder.indexOf(currentScreen);
        if (currentIndex > 0) {
            setCurrentScreen(screenOrder[currentIndex - 1]);
        }
    };
    const renderScreen = () => {

        switch (currentScreen) {
            case 'home':
                return (
                    <HomeScreen
                        currentProgress={1}
                        totalScreens={15}
                        onNext={() => navigateTo('gender')}
                        onBack={goBack}
                        // onNavigateToLogin={() => navigateTo('login')}
                        navigate={navigateTo}
                />
                );
            case 'login':
                return (
                    <LoginScreen

                        onNext={() => navigateTo('home')}
                        onBack={() => navigateTo('home')}
                    />
                );
            case 'money':
                return (
                    <MoneyScreen

                        onNext={() => navigateTo('home')}
                        onBack={() => navigateTo('home')}
                        // onNavigateToLogin={() => navigateTo('login')}
                        navigate={navigateTo}
                    />
                );
            case 'billing':
                return (
                    <BillingScreen
                        onNext={() => navigateTo('home')}
                        onBack={() => navigateTo('home')}
                        // onNavigateToLogin={() => navigateTo('login')}
                        navigate={navigateTo}
                    />
                );
            case 'termsOfUse':
                return (
                    <TermsOfUseScreen

                        onNext={() => navigateTo('home')}
                        onBack={() => navigateTo('home')}
                        // onNavigateToLogin={() => navigateTo('login')}
                        navigate={navigateTo}
                    />
                );
            case 'privacy':
                return (
                    <PrivacyScreen
                        onNext={() => navigateTo('home')}
                        onBack={() => navigateTo('home')}
                        // onNavigateToLogin={() => navigateTo('login')}
                        navigate={navigateTo}
                    />
                );
            case 'gender':
                return (
                    <GenderSelectScreen
                        currentProgress={2}
                        totalScreens={15}
                        updateUserData={updateUserData}
                        onNext={() => navigateTo('birthday')}
                        onBack={goBack}
                    />
                );
            case 'birthday':
                return (
                    <BirthdayScreen
                        currentProgress={3}
                        totalScreens={15}
                        onNext={() => navigateTo('birthtime')}
                        onBack={goBack}
                        updateUserData={updateUserData}
                        userData={userData}
                    />
                );
            case 'birthtime':
                return (
                    <TimePickerScreen
                        currentProgress={4}
                        totalScreens={15}
                        onNext={() => navigateTo('city')}
                        onBack={goBack}
                        updateUserData={updateUserData}
                        userData={userData}
                    />
                );
            case 'city':
                return (
                    <CityScreen
                        currentProgress={5}
                        totalScreens={15}
                        onNext={() => navigateTo('astromap')}
                        onBack={goBack}
                        updateUserData={updateUserData}
                        userData={userData}
                    />
                );
            case 'astromap':
                return (
                    <AstroMapScreen
                        currentProgress={6}
                        totalScreens={15}
                        onNext={() => navigateTo('forecast')}
                        userData={userData}
                        onBack={goBack}
                    />
                );

            case 'forecast':
                return (
                    <ForecastScreen
                        currentProgress={7}
                        totalScreens={15}
                        onNext={() => navigateTo('relationship')}
                        onBack={goBack}
                    />
                );
            case 'relationship':
                return (
                    <RelationshipScreen
                        currentProgress={8}
                        totalScreens={15}
                        onNext={() => navigateTo('goals')}
                        onBack={goBack}
                        updateUserData={updateUserData}
                    />
                );
            case 'goals':
                return (
                    <GoalsScreen
                        currentProgress={9}
                        totalScreens={15}
                        onNext={() => navigateTo('colors')}
                        onBack={goBack}
                        updateUserData={updateUserData}

                    />
                );
            case 'colors':
                return (
                    <ColorsScreen
                        currentProgress={10}
                        totalScreens={15}
                        onNext={() => navigateTo('naturesElements')}
                        onBack={goBack}
                    />
                );
            case 'naturesElements':
                return (
                    <NaturesElementsScreen
                        currentProgress={11}
                        totalScreens={15}
                        onNext={() => navigateTo('peopleCard')}
                        onBack={goBack}
                    />
                );
            case 'peopleCard':
                return (
                    <PeopleCardScreen
                        currentProgress={12}
                        totalScreens={15}
                        onNext={() => navigateTo('forecastSecond')}
                        onBack={goBack}
                        userData={userData}
                    />
                );
            case 'forecastSecond':
                return (
                    <ForecastSecondScreen
                        currentProgress={13}
                        totalScreens={15}
                        onNext={() => navigateTo('handscan')}
                        onBack={goBack}
                    />
                );
            case 'handscan':
                return (
                    <HandScanScreen
                        currentProgress={14}
                        totalScreens={15}
                        onNext={() => navigateTo('forecastThird')}
                        onUploadPhoto={() => navigateTo('photoUpload')}
                        onBack={goBack}
                    />
                );
            case 'photoUpload': //   case
                return (
                    <PhotoUploadScreen
                        currentProgress={14}
                        totalScreens={15}
                        onNext={() => navigateTo('forecastThird')}
                        onBack={goBack}
                    />
                );
            case 'forecastThird':
                return (
                    <ForecastThirdScreen
                        currentProgress={15}
                        totalScreens={15}
                        onNext={() => navigateTo('report')}
                        onBack={goBack}
                    />
                );
            case 'report':
                return (
                    <ReportScreen
                        currentProgress={15}
                        totalScreens={15}
                        onNext={() => navigateTo('home')}
                        onBack={goBack}
                    />
                );

            default:
                return <HomeScreen
                    currentProgress={1}
                    totalScreens={15}
                    onNext={() => navigateTo('forecast')} />;
        }
    };

    return (
        <div className="app">
            <div className="container">
                    {renderScreen()}
            </div>
        </div>
    );
}

export default App;