import React, {useState, useEffect, useRef} from 'react';
import './hamburgerMenu.css';
const HamburgerMenu = ({navigate}) => {
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const [shouldRenderMenu, setShouldRenderMenu] = useState(false);
    const sidebarRef = useRef(null);
    const hamburgerRef = useRef(null);
    const containerRef = useRef(null);

    //  
    const openMenu = () => {
        setShouldRenderMenu(true);
        setTimeout(() => {
            setIsMenuOpen(true);
        }, 10);
    };

    //  
    const closeMenu = () => {
        setIsMenuOpen(false);
        setTimeout(() => {
            setShouldRenderMenu(false);
        }, 300);
    };

    //  
    const toggleMenu = () => {
        if (isMenuOpen) {
            closeMenu();
        } else {
            openMenu();
        }
    };

    //      
    useEffect(() => {
        const handleClickOutside = (event) => {
            if (
                isMenuOpen &&
                sidebarRef.current &&
                !sidebarRef.current.contains(event.target) &&
                hamburgerRef.current &&
                !hamburgerRef.current.contains(event.target)
            ) {
                closeMenu();
            }
        };

        document.addEventListener('click', handleClickOutside);
        return () => {
            document.removeEventListener('click', handleClickOutside);
        };
    }, [isMenuOpen]);

    //     Escape
    useEffect(() => {
        const handleEscape = (event) => {
            if (event.key === 'Escape' && isMenuOpen) {
                closeMenu();
            }
        };

        document.addEventListener('keydown', handleEscape);
        return () => document.removeEventListener('keydown', handleEscape);
    }, [isMenuOpen]);

    return (
        <>
            {/*  - -        */}
            <button
                ref={hamburgerRef}
                className="hamburger-menu"
                aria-label="Menu Toggle"
                onClick={toggleMenu}
                aria-expanded={isMenuOpen}
                style={{
                    position: 'absolute',
                    top: '0',
                    right: '0',
                    zIndex: '100'
                }}
            >
                <i>
                    <img src="/assets/img/list.svg" alt="Menu"/>
                </i>
            </button>

            {/*     */}
            {shouldRenderMenu && (
                <div
                    className={`menu-overlay animate__animated ${isMenuOpen ? 'animate__fadeIn' : 'animate__fadeOut'}`}
                    onClick={closeMenu}
                />
            )}

            {/*   -     */}
            {shouldRenderMenu && (
                <nav
                    ref={sidebarRef}
                    className={`sidebar-menu ${isMenuOpen ? 'open' : ''}`}
                    aria-hidden={!isMenuOpen}
                    style={{
                        position: 'absolute',
                        top: '0',
                        right: isMenuOpen ? '0' : '-250px', //     
                        width: '250px',
                        transition: 'right 0.3s ease-in-out',
                        zIndex: '1000'
                    }}
                >
                    <button
                        className="close-btn"
                        onClick={closeMenu}
                        aria-label="Close menu"
                    >
                        
                    </button>

                    <ul>
                        <li><a onClick={() => navigate('privacy')}>Privacy Policy</a></li>
                        <li><a onClick={() => navigate('termsOfUse')}>Terms of Use</a></li>
                        <li><a onClick={() => navigate('billing')}>Billing Terms</a></li>
                        <li><a onClick={() => navigate('money')}>Money-Back Policy</a></li>
                        <li><a onClick={() => navigate('login')}>My Account</a></li>
                    </ul>
                </nav>
            )}
        </>
    );
};

export default HamburgerMenu;