import React from 'react';
import AnimatedCounter from '../components/animatedCounter';
import ProgressBar from "../components/progressBar";
const ForecastScreen = ({ onNext,onBack,currentProgress,totalScreens }) => {
    return (
        <div className="App animate__animated animate__fadeIn">

            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text">{currentProgress}/{totalScreens} </div>
            </header>
            <ProgressBar currentStep={currentProgress} totalSteps={totalScreens} />

            <main>
                <h2 style={{fontFamily: 'Philosopher' }}>Forecast accuracy</h2>
                <div style={{ textAlign: 'center', margin: '20px 0',fontSize: '24px', fontFamily: 'Philosopher' }}>
                    <AnimatedCounter target={34} duration={4000} />
                </div>
                <div className="astromap-container">
                    <img className="progress-bar-circle-bg" src="/assets/img/astromap-background.svg" alt=""/>
                    <img className="progress-bar-circle" src="/assets/img/test.svg" alt=""/>
                </div>
                <div className="message animate__animated animate__fadeInUp" style={{ animationDelay: '5s' }}>
                    <div className="message-text">
                        Your charts shows a <span className="blue-text">rare spark</span> - let`s uncover how you can
                        use this power!
                    </div>
                </div>
                <p className="sub-heading animate__animated animate__fadeInUp" style={{ animationDelay: '5s' }}>
                    <img src="/women.webp" alt="" className="message-avatar"/>
                </p>
            </main>
            <div className="button-container animate__animated animate__fadeIn" style={{ animationDelay: '5s' }}>
                <button className="next-btn" onClick={onNext}>
                    Continue
                </button>
            </div>
        </div>

    );
};

export default ForecastScreen;