import React, { useState, useEffect } from 'react';

const LoginScreen = ({ onNext, onBack, updateUserData }) => {
    const [isButtonVisible, setIsButtonVisible] = useState(false);
    const [email, setEmail] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');
    const [showSubmitButton, setShowSubmitButton] = useState(false);

    useEffect(() => {
        const timer = setTimeout(() => {
            setIsButtonVisible(true);
        }, 300);
        return () => clearTimeout(timer);
    }, []);

    //   email   
    useEffect(() => {
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        const hasEmail = email.trim().length > 0;
        const isValidEmail = emailRegex.test(email);
        setShowSubmitButton(hasEmail && isValidEmail);
    }, [email]);

    //    email  Spring 
    const sendEmailToBackend = async (userEmail) => {
        try {
            //  Spring  endpoint
            const response = await fetch('http://localhost:8080/api/auth/send-access-link', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    //   :
                    // 'Authorization': `Bearer ${localStorage.getItem('token')}`
                },
                body: JSON.stringify({
                    email: userEmail,
                    //  ,  :
                    appName: 'Atrix',
                    redirectUrl: window.location.origin + '/dashboard',
                    timestamp: new Date().toISOString()
                })
            });

            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }

            const data = await response.json();
            return data;
        } catch (error) {
            console.error('Error sending email:', error);
            throw error;
        }
    };

    const handleContinue = async () => {
        if (!email.trim()) {
            setError('Please enter your email');
            return;
        }

        //  email
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(email)) {
            setError('Please enter a valid email address');
            return;
        }

        //   
        setError('');
        setSuccess('');
        setIsLoading(true);

        try {
            // 1.  email  
            const response = await sendEmailToBackend(email);

            // 2.  email  localStorage ()
            localStorage.setItem('userEmail', email);

            // 3.   userData  updateUserData 
            if (updateUserData) {
                updateUserData({
                    email: email,
                    emailSent: true,
                    emailSentAt: new Date().toISOString()
                });
            }

            // 4.   
            setSuccess(`Access link has been sent to ${email}. Please check your email.`);

            // 5.  
            console.log('Email sent successfully:', {
                email,
                response,
                timestamp: new Date().toISOString()
            });

            // 6.    3  ()
            setTimeout(() => {
                if (onNext) {
                    onNext();
                }
            }, 3000);

        } catch (error) {
            //  
            console.error('Failed to send email:', error);

            //   
            if (error.message.includes('409')) {
                setError('This email is already registered. Please use a different email or try to login.');
            } else if (error.message.includes('400')) {
                setError('Invalid email format. Please check and try again.');
            } else if (error.message.includes('429')) {
                setError('Too many requests. Please try again in a few minutes.');
            } else if (error.message.includes('500')) {
                setError('Server error. Please try again later.');
            } else if (error.name === 'TypeError' && error.message.includes('fetch')) {
                setError('Network error. Please check your connection and try again.');
            } else {
                setError('Failed to send access link. Please try again.');
            }

        } finally {
            setIsLoading(false);
        }
    };

    //   Enter
    const handleKeyPress = (e) => {
        if (e.key === 'Enter' && showSubmitButton && !isLoading) {
            handleContinue();
        }
    };

    return (
        <div className="App animate__animated animate__fadeIn">
            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text"></div>
            </header>

            <main style={{overflow: "hidden"}}>
                <p className="title mt-24 font-size-24 philosopher-bold">
                    My Account
                </p>
                <p className="sub-heading">
                    Need help finding the personal link to access the app? Please enter your email  we will send you a link to access your account information.
                </p>

                <div className="input-container">
                    <input
                        className="email-input"
                        type="email"
                        id="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        onKeyPress={handleKeyPress}
                        placeholder="johndoe@gmail.com"
                        autoFocus
                        disabled={isLoading}
                    />

                    <div className="sub-heading">
                        To provide the requested information, enter the email address you used at checkout when purchasing your subscription plan
                    </div>
                </div>

                {/*      */}
                {error && (
                    <div className="error-message animate__animated animate__fadeIn">
                        {error}
                    </div>
                )}

                {success && (
                    <div className="success-message animate__animated animate__fadeIn">
                        {success}
                    </div>
                )}
            </main>

            <div className="button-container">
                {showSubmitButton && (
                    <button
                        className={`next-btn ${isLoading ? 'loading' : ''}`}
                        onClick={handleContinue}
                        disabled={isLoading || !showSubmitButton}
                        style={{
                            opacity: isButtonVisible ? 1 : 0,
                            transform: isButtonVisible ? 'translateY(0)' : 'translateY(20px)',
                            transition: 'opacity 0.3s, transform 0.3s',
                            position: 'relative'
                        }}
                    >
                        {isLoading ? (
                            <>
                                <span className="spinner"></span>
                                Sending...
                            </>
                        ) : (
                            'Send Access Link'
                        )}
                    </button>
                )}
            </div>
        </div>
    );
};

export default LoginScreen;