import React, { useState } from 'react';
import ProgressBar from "../components/progressBar";

const GenderSelectScreen = ({ onNext, onBack, currentProgress, totalScreens, updateUserData }) => {
    const [selectedGender, setSelectedGender] = useState('');

    const genders = [
        { value: 'female', symbol: '', label: 'Female' },
        { value: 'male', symbol: '', label: 'Male' },
        { value: 'other', symbol: '', label: 'Non-binary' }
    ];

    const handleSelect = (genderValue) => {
        console.log(' Gender selected:', genderValue);
        setSelectedGender(genderValue);

        //    
        if (updateUserData) {
            updateUserData({ gender: genderValue });
        }

        //  
        if (onNext) {
            onNext();
        }
    };

    return (
        <div className="App animate__animated animate__fadeIn">
            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text">{currentProgress}/{totalScreens}</div>
            </header>

            <ProgressBar currentStep={currentProgress} totalSteps={totalScreens} />
            <main
                style={{overflow: "hidden"}}>
                <p className="title mt-24 font-size-24 philosopher-bold">
                    Personalized astrology report with powerful predictions
                </p>
                <p className="sub-heading">
                    Select your gender
                </p>

                <div className="gender-selection">
                    {genders.map((gender) => (
                        <div
                            key={gender.value}
                            className={`gender-card ${selectedGender === gender.value ? 'selected' : ''}`}
                            onClick={() => handleSelect(gender.value)}
                        >
                            <span className="symbol">{gender.symbol}</span>
                            <span className="label">{gender.label}</span>
                        </div>
                    ))}
                </div>
            </main>
        </div>
    );
};

export default GenderSelectScreen;