import React, { useState, useEffect, useRef } from 'react';
import ProgressBar from "../components/progressBar";

const TimePickerScreen = ({onBack, currentProgress, totalScreens, onNext, updateUserData, userData }) => {
    const [selectedTime, setSelectedTime] = useState({
        hour: '12',
        minute: '00',
        period: 'AM'
    });

    const hourRef = useRef(null);
    const minuteRef = useRef(null);
    const periodRef = useRef(null);

    //  
    const hours = Array.from({ length: 12 }, (_, i) => (i + 1).toString().padStart(2, '0'));
    const minutes = Array.from({ length: 60 }, (_, i) => i.toString().padStart(2, '0'));
    const periods = ['AM', 'PM'];

    //  
    useEffect(() => {
        const updateScrollPosition = (pickerRef, value, items) => {
            const index = items.indexOf(value);
            if (index >= 0 && pickerRef.current) {
                pickerRef.current.scrollTop = (index + 2) * 50;
            }
        };

        updateScrollPosition(hourRef, selectedTime.hour, hours);
        updateScrollPosition(minuteRef, selectedTime.minute, minutes);
        updateScrollPosition(periodRef, selectedTime.period, periods);

        //   
        setTimeout(() => {
            if (hourRef.current) handleScroll(hourRef, 'hour');
            if (minuteRef.current) handleScroll(minuteRef, 'minute');
            if (periodRef.current) handleScroll(periodRef, 'period');
        }, 100);
    }, []);

    //  
    const handleScroll = (pickerRef, type) => {
        const picker = pickerRef.current;
        if (!picker) return;

        const options = picker.querySelectorAll('.picker-option');
        const pickerRect = picker.getBoundingClientRect();
        const centerY = pickerRect.top + pickerRect.height / 2;

        let selectedValue = selectedTime[type];

        options.forEach(option => {
            const optionRect = option.getBoundingClientRect();
            const optionCenterY = optionRect.top + optionRect.height / 2;
            const distance = Math.abs(centerY - optionCenterY);

            if (distance < 25 && option.textContent) {
                option.classList.add('active');
                selectedValue = option.textContent;
            } else {
                option.classList.remove('active');
            }
        });

        if (selectedValue !== selectedTime[type]) {
            setSelectedTime(prev => ({
                ...prev,
                [type]: selectedValue
            }));
        }
    };

    //   
    useEffect(() => {
        const handlePickerScroll = () => {
            if (hourRef.current) handleScroll(hourRef, 'hour');
            if (minuteRef.current) handleScroll(minuteRef, 'minute');
            if (periodRef.current) handleScroll(periodRef, 'period');
        };

        const hourPicker = hourRef.current;
        const minutePicker = minuteRef.current;
        const periodPicker = periodRef.current;

        [hourPicker, minutePicker, periodPicker].forEach(picker => {
            picker?.addEventListener('scroll', handlePickerScroll);
        });

        return () => {
            [hourPicker, minutePicker, periodPicker].forEach(picker => {
                picker?.removeEventListener('scroll', handlePickerScroll);
            });
        };
    }, [selectedTime]);

    const handleContinue = () => {
        console.log(' Continue button clicked - time selection');

        //   
        const timeString = `${selectedTime.hour}:${selectedTime.minute} ${selectedTime.period}`;
        console.log('Selected time:', timeString);

        //    userData
        const birthdayDate = userData?.birthday || new Date('April 1, 2000');

        //     
        const fullDateTime = new Date(birthdayDate);

        //  
        let hour24 = parseInt(selectedTime.hour);
        if (selectedTime.period === 'PM' && hour24 !== 12) {
            hour24 += 12;
        } else if (selectedTime.period === 'AM' && hour24 === 12) {
            hour24 = 0;
        }

        fullDateTime.setHours(hour24);
        fullDateTime.setMinutes(parseInt(selectedTime.minute));
        fullDateTime.setSeconds(0);
        fullDateTime.setMilliseconds(0);

        //      
        const fullDateTimeString = `${userData.birthdayString || 'April 1, 2000'} ${timeString}`;

        console.log('Full date and time:', fullDateTimeString);
        console.log('Date object:', fullDateTime);

        //       userData
        if (updateUserData && typeof updateUserData === 'function') {
            updateUserData({
                birthTime: timeString,
                birthDateTime: fullDateTime,
                birthDateTimeString: fullDateTimeString,
                knowsBirthTime: true
            });
            console.log(' Time saved to userData');
        }

        //    
        if (onNext && typeof onNext === 'function') {
            console.log(' Calling onNext');
            setTimeout(() => {
                onNext();
            }, 300);
        } else {
            console.error(' onNext is not available');
        }
    };

    const renderPickerOptions = (items) => {
        return [
            ...Array(2).fill(null).map((_, i) =>
                <div key={`empty-top-${i}`} className="picker-option"></div>
            ),
            ...items.map((item, index) => (
                <div
                    key={index}
                    className="picker-option"
                >
                    {item}
                </div>
            )),
            ...Array(2).fill(null).map((_, i) =>
                <div key={`empty-bottom-${i}`} className="picker-option"></div>
            )
        ];
    };

    //  "I don't know"
    const handleDontKnow = () => {
        console.log(' User doesn\'t know birth time');

        if (updateUserData && typeof updateUserData === 'function') {
            updateUserData({
                birthTime: null,
                birthDateTime: null,
                birthDateTimeString: null,
                knowsBirthTime: false
            });
            console.log(' Marked as unknown time');
        }

        if (onNext && typeof onNext === 'function') {
            setTimeout(() => {
                onNext();
            }, 300);
        }
    };

    return (
        <div className="App animate__animated animate__fadeIn">
            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text">{currentProgress}/{totalScreens}</div>
            </header>

            <ProgressBar currentStep={currentProgress} totalSteps={totalScreens} />

            <main>
                <div className="birthday-picker-container">
                    <div className="container">
                        <h1 className="title">What time were you born?</h1>
                        <p className="subtitle">
                            Knowing your exact birth time helps create<br />
                            the most accurate astrological chart
                        </p>

                        <div className="picker-container">
                            <div className="picker-highlight"></div>

                            <div className="picker-column">
                                <div className="picker-label">Hour</div>
                                <div className="picker-wrapper" ref={hourRef}>
                                    {renderPickerOptions(hours)}
                                </div>
                            </div>

                            <div className="picker-column">
                                <div className="picker-label">Minute</div>
                                <div className="picker-wrapper" ref={minuteRef}>
                                    {renderPickerOptions(minutes)}
                                </div>
                            </div>

                            <div className="picker-column">
                                <div className="picker-label">AM/PM</div>
                                <div className="picker-wrapper" ref={periodRef}>
                                    {renderPickerOptions(periods)}
                                </div>
                            </div>
                        </div>

                        <div className="time-info">
                            <p>{selectedTime.hour}:{selectedTime.minute} {selectedTime.period}</p>
                            {userData?.birthdayString && (
                                <p className="date-preview">
                                    {userData.birthdayString}
                                </p>
                            )}
                        </div>
                    </div>
                </div>
            </main>

            <div className="button-container">

                <p className="secondary-link animate__animated animate__fadeIn"
                   style={{ animationDelay: '0.5s' }}
                    onClick={handleDontKnow}>

                    I don't remember
                </p>
                <button
                    className="next-btn animate__animated animate__fadeIn"
                    style={{ animationDelay: '3s' }}
                    onClick={handleContinue}
                >
                    Continue
                </button>
            </div>
        </div>
    );
};

export default TimePickerScreen;