import React from 'react';
import HamburgerMenu from "../components/hamburgerMenu/hamburgerMenu.js";

const HomeScreen = ({ onNext,onBack,currentProgress,totalScreens, onNavigateToLogin, navigate  }) => {
    return (

    <div className="App">
            <main style={{overflow:"hidden"}}>
                <div>
                    <div style={{ position: 'relative', display: 'flex', justifyContent: 'end' }}>
                        {/*<HamburgerMenu onNavigateToLogin={onNavigateToLogin} />*/}
                        <HamburgerMenu navigate={navigate}/>
                    </div>
                </div>
                <p className="title mt-64 font-size-24 philosopher-bold">
                    Get palm predictions and know yourself better
                </p>
                <p className="sub-heading">
                    The element of nature is important for better personalization
                </p>
                <div className="card-container">
                    <img src="/assets/img/homehand.svg" alt=""/>
                </div>
                <div className="button-container">
                    <button className="next-btn animate__animated animate__fadeIn" onClick={onNext}>
                        Continue
                    </button>
                </div>
            </main>
        </div>
    );
};

export default HomeScreen;