import React, { useEffect, useState } from 'react';

const ReportScreen = ({ userData, onNext, onBack, currentProgress, totalScreens, onNavigateToLogin, navigate }) => {
    const [photoPreview, setPhotoPreview] = useState(null);
    const [debugInfo, setDebugInfo] = useState('');

    // :     userData
    useEffect(() => {

        if (userData) {

            //   
            setDebugInfo(`
                UserData exists: ${!!userData}
                Has userPhoto property: ${!!userData.userPhoto}
                Has photoPreview: ${!!userData.userPhoto?.photoPreview}
                PhotoPreview type: ${typeof userData.userPhoto?.photoPreview}
                PhotoPreview starts with data: ${userData.userPhoto?.photoPreview?.startsWith('data:')}
            `);
        }
    }, [userData]);

    //     
    useEffect(() => {
        // 1.    userData.userPhoto
        if (userData?.userPhoto?.photoPreview) {
            setPhotoPreview(userData.userPhoto.photoPreview);
            return;
        }

        // 2.    userData.photo (  )
        if (userData?.photo && typeof userData.photo === 'string' && userData.photo.startsWith('data:image/')) {
            setPhotoPreview(userData.photo);
            return;
        }

        // 3.    localStorage ( )
        const localStoragePhoto = localStorage.getItem('atrix_user_photo');
        if (localStoragePhoto) {
            setPhotoPreview(localStoragePhoto);
            return;
        }

        // 4.  sessionStorage
        const sessionStoragePhoto = sessionStorage.getItem('atrix_user_photo');
        if (sessionStoragePhoto) {
            setPhotoPreview(sessionStoragePhoto);
            return;
        }

        setPhotoPreview(null);
    }, [userData]);

    //     localStorage
    const checkLocalStorage = () => {
        const photo = localStorage.getItem('atrix_user_photo');
        console.log('  localStorage:', photo ? ' ' : '  ');
        if (photo) {
            setPhotoPreview(photo);
        }
    };

    return (

        <div className="App">
            <div className="container">
                <header className="report-header">
                    Your palm reading report is ready
                </header>

                <main className="report-box">
                    <div className="report-content-wrapper">
                        {photoPreview ? (

                            <div className="hand-visual">
                            <img  src={photoPreview}   alt="Hand with palm lines" className="palm-image"/>
                            </div>
                        ):(
                            <div className="hand-visual">
                                <img  src="/assets/img/life-lines.jpg"   alt="Hand with palm lines" className="palm-image"/>
                            </div>
                            )}
                        <div className="stats-placeholder">
                            <h2>OVERVIEW</h2>
                            <div className="stat-item">
                                <span className="stat-label">Life: 83%</span>
                                <div className="progress-bar-stat-container">
                                    <div className="progress-bar life" style={{width: '83%'}}></div>
                                </div>
                            </div>
                            <div className="stat-item">
                                <span className="stat-label">Health: 71%</span>
                                <div className="progress-bar-stat-container">
                                    <div className="progress-bar health" style={{width: '71%'}}></div>
                                </div>
                            </div>
                            <div className="stat-item">
                                <span className="stat-label">Wisdom: 81%</span>
                                <div className="progress-bar-stat-container">
                                    <div className="progress-bar wisdom" style={{width: '81%'}}></div>
                                </div>
                            </div>
                            <div className="stat-item">
                                <span className="stat-label">Career: 56%</span>
                                <div className="progress-bar-stat-container">
                                    <div className="progress-bar career" style={{width: '56%'}}></div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <p className="report-summary">Your <span className="highlight-line">Heart Line</span> demonstrates
                        your great control.</p>
                </main>

                <section className="signup-section">
                    <h2>Sign up to understand yourself better with Atrix</h2>
                    <form>
                        <input type="email"  placeholder="youremail@gmail.com"/>
                        <div className="security-info">
                            <span className="security-icon" aria-label="Security icon"></span>
                            <p>Your personal data is safe with us. We'll use your email for updates, receipts, and
                                subscription details.</p>
                        </div>
                        <button type="submit">Continue</button>
                    </form>
                </section>
            </div>
        </div>
    );
};

export default ReportScreen;