import React from 'react';
import { useState } from 'react';
import ProgressBar from "../components/progressBar";


const ColorsScreen = ({ onNext,onBack, currentProgress, totalScreens  }) => {

    return (

        <div className="App animate__animated animate__fadeIn">

            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text">{currentProgress}/{totalScreens}</div>
            </header>

            <ProgressBar currentStep={10} />

            <main>
                <p className="title font-size-24 philosopher-bold">
                    Which of the following <span className="blue-text philosopher-bold">colors</span> do you prefer?
                </p>
                <p className="sub-heading">
                    The color is important for better personalization
                </p>
                <div className="card-container">
                    <div className="card-content" onClick={onNext} >
                        <div className="card-logo">
                            <div className="circle bg-red"></div>
                        </div>
                        <div className="card-text philosopher-bold">Red</div>
                    </div>
                    <div className="card-content" onClick={onNext} >
                        <div className="card-logo">
                            <div className="circle bg-yellow"></div>
                        </div>
                        <div className="card-text philosopher-bold">Yellow</div>
                    </div>
                    <div className="card-content" onClick={onNext} >
                        <div className="card-logo">
                            <div className="circle bg-blue"></div>
                        </div>
                        <div className="card-text philosopher-bold">Blue</div>
                    </div>
                    <div className="card-content" onClick={onNext} >
                        <div className="card-logo">
                            <div className="circle bg-orange"></div>

                        </div>
                        <div className="card-text philosopher-bold">Orange</div>
                    </div>
                    <div className="card-content" onClick={onNext} >
                        <div className="card-logo">
                            <div className="circle bg-green"></div>

                        </div>
                        <div className="card-text philosopher-bold">Green</div>
                    </div>
                    <div className="card-content" onClick={onNext} >
                        <div className="card-logo">
                            <div className="circle bg-violet"></div>

                        </div>
                        <div className="card-text philosopher-bold">Violet</div>
                    </div>
                   

                </div>
            </main>
            <div className="button-container">

            </div>
        </div>
    );
};

export default ColorsScreen;