import React, { useState, useEffect } from 'react';
import ProgressBar from "../components/progressBar";

const GoalsScreen = ({ onNext, onBack, currentProgress, totalScreens, updateUserData }) => {
    const [selectedCards, setSelectedCards] = useState([]);
    const [showButton, setShowButton] = useState(false);

    const cards = [
        { id: 1, logo: "", text: "Family harmony", value: "family_harmony" },
        { id: 2, logo: "", text: "Career", value: "career" },
        { id: 3, logo: "", text: "Health", value: "health" },
        { id: 4, logo: "", text: "Getting married", value: "getting_married" },
        { id: 5, logo: "", text: "Traveling the world", value: "traveling" },
        { id: 6, logo: "", text: "Education", value: "education" },
        { id: 7, logo: "", text: "Friends", value: "friends" },
        { id: 8, logo: "", text: "Children", value: "children" }
    ];

    //    userData   
    useEffect(() => {
        if (selectedCards.length > 0) {
            const selectedGoals = cards
                .filter(card => selectedCards.includes(card.id))
                .map(card => ({
                    id: card.id,
                    value: card.value,
                    text: card.text,
                    logo: card.logo
                }));

            if (updateUserData && typeof updateUserData === 'function') {
                updateUserData({
                    goals: selectedGoals,
                    goalsCount: selectedCards.length
                });
                console.log(' Goals saved:', selectedGoals.map(g => g.text));
            }
        }
    }, [selectedCards]);

    const handleCardClick = (cardId) => {
        console.log("Clicked card ID:", cardId);

        let newSelectedCards;

        if (selectedCards.includes(cardId)) {
            //     
            newSelectedCards = selectedCards.filter(id => id !== cardId);
        } else if (selectedCards.length < 3) {
            //    
            newSelectedCards = [...selectedCards, cardId];
        } else {
            //   3   -  
            newSelectedCards = selectedCards;
        }

        console.log("New selection:", newSelectedCards);
        setSelectedCards(newSelectedCards);
        setShowButton(newSelectedCards.length > 0);
    };

    const handleContinue = () => {
        if (selectedCards.length === 0) {
            alert('Please select at least one goal');
            return;
        }

        console.log(' Final goals selected:', selectedCards);

        //      
        const selectedGoals = cards
            .filter(card => selectedCards.includes(card.id))
            .map(card => ({
                id: card.id,
                value: card.value,
                text: card.text,
                logo: card.logo
            }));

        if (updateUserData) {
            updateUserData({
                goals: selectedGoals,
                goalsCount: selectedCards.length,
                goalsFinal: true
            });
            console.log(' Final goals saved to userData');
        }

        //    
        if (onNext) {
            setTimeout(() => {
                onNext();
            }, 300);
        }
    };

    const isCardSelected = (cardId) => selectedCards.includes(cardId);
    const isCardDisabled = (cardId) => selectedCards.length >= 3 && !selectedCards.includes(cardId);

    //      
    const getSelectedGoalsText = () => {
        return cards
            .filter(card => selectedCards.includes(card.id))
            .map(card => card.text)
            .join(', ');
    };

    return (
        <div className="App animate__animated animate__fadeIn">
            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text">{currentProgress}/{totalScreens}</div>
            </header>

            <ProgressBar currentStep={currentProgress} totalSteps={totalScreens} />

            <main>
                <p className="title font-size-24 philosopher-bold">
                    What are your <span className="blue-text philosopher-bold">goals</span> for the future?
                </p>

                <div className="sub-heading mt-16">
                    <span>
                        {selectedCards.length}/3
                    </span>

                </div>

                <div className="card-container">
                    {/*   */}
                    <div className="card-row">
                        {cards.slice(0, 3).map(card => (
                            <button
                                key={card.id}
                                className={`card-btn ${isCardSelected(card.id) ? 'selected' : ''} ${isCardDisabled(card.id) ? 'disabled' : ''}`}
                                onClick={() => handleCardClick(card.id)}
                                disabled={isCardDisabled(card.id)}
                                title={isCardDisabled(card.id) ? "Maximum 3 goals allowed" : ""}
                            >
                                <span className="card-btn-logo">{card.logo}</span>
                                <span className="card-btn-text philosopher-bold">{card.text}</span>

                            </button>
                        ))}
                    </div>

                    {/*   */}
                    <div className="card-row">
                        {cards.slice(3, 5).map(card => (
                            <button
                                key={card.id}
                                className={`card-btn ${isCardSelected(card.id) ? 'selected' : ''} ${isCardDisabled(card.id) ? 'disabled' : ''}`}
                                onClick={() => handleCardClick(card.id)}
                                disabled={isCardDisabled(card.id)}
                            >
                                <span className="card-btn-logo">{card.logo}</span>
                                <span className="card-btn-text philosopher-bold">{card.text}</span>

                            </button>
                        ))}
                    </div>

                    {/*   */}
                    <div className="card-row">
                        {cards.slice(5, 8).map(card => (
                            <button
                                key={card.id}
                                className={`card-btn ${isCardSelected(card.id) ? 'selected' : ''} ${isCardDisabled(card.id) ? 'disabled' : ''}`}
                                onClick={() => handleCardClick(card.id)}
                                disabled={isCardDisabled(card.id)}
                            >
                                <span className="card-btn-logo">{card.logo}</span>
                                <span className="card-btn-text philosopher-bold">{card.text}</span>

                            </button>
                        ))}
                    </div>
                </div>


            </main>

            <div className="button-container">
                {showButton && (
                    <div className="mt-16">
                        <button
                            className="next-btn"
                            onClick={handleContinue}
                        >Continue
                        </button>
                    </div>
                )}
            </div>
        </div>
    );
};

export default GoalsScreen;