import React from 'react';
import ProgressBar from "../components/progressBar";

const NaturesElementsScreen = ({ onNext,onBack,currentProgress,totalScreens }) => {
    return (
        <div className="App animate__animated animate__fadeIn">

            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text">{currentProgress}/{totalScreens}</div>
            </header>

            <ProgressBar currentStep={currentProgress} totalSteps={totalScreens} />


            <main>
                <p className="title font-size-24 philosopher-bold">
                    To get started, tell us about your current <span className="blue-text philosopher-bold">relationship status</span>
                </p>
                <p className="sub-heading">
                    The element of nature is important for better personalization
                </p>
                <div className="card-container">
                    <div className="card-content" onClick={onNext} >
                        <div className="card-logo">
                            <img src="/assets/img/earth_dark.webp" alt=""/>
                        </div>
                        <div className="card-text philosopher-bold">Earth</div>
                    </div>
                    <div className="card-content" onClick={onNext} >
                        <div className="card-logo">
                            <img src="/assets/img/water_dark.webp" alt=""/>
                        </div>
                        <div className="card-text philosopher-bold">Water</div>
                    </div>
                    <div className="card-content" onClick={onNext} >
                        <div className="card-logo">
                            <img src="/assets/img/fire_dark.webp" alt=""/>
                        </div>
                        <div className="card-text philosopher-bold">Fire</div>
                    </div>
                    <div className="card-content" onClick={onNext} >
                        <div className="card-logo">
                            <img src="/assets/img/air_dark.webp" alt=""/>
                        </div>
                        <div className="card-text philosopher-bold">Air</div>
                    </div>

                </div>
            </main>
        </div>
    );
};

export default NaturesElementsScreen;