import React from 'react';
import '../styles/money.css'
const MoneyScreen = ({ onNext,onBack,navigate }) => {
    return (

        <div>
            <header className="second-header">
                <div className="top-nav-bar">
                    <div className="back-button"></div>
                    <div className="nav-links">
                        <button className="back-arrow" onClick={onBack}></button>
                        <a onClick={() => navigate('termsOfUse')}>Terms of Use</a>
                        <a onClick={() => navigate('Privacy')}>Privacy</a>
                        <a onClick={() => navigate('billing')}>Billing Terms</a>
                    </div>
                </div>
            </header>

            <main className="content-container">
                <h1 className="main-title">MONEY-BACK POLICY</h1>
                <p className="updated-date">Updated and Effective as of July 2024</p>

                <p className="policy-paragraph">
                    This Money-Back Policy describes how users can cancel a subscription and refund their money if they
                    have purchased a subscription on the website. The Money-Back Policy is incorporated into and subject
                    to the Terms of Use unless otherwise provided for therein.
                </p>

                <h2 className="section-title">1. HOW TO CANCEL YOUR SUBSCRIPTION</h2>
                <p className="policy-paragraph">
                    If you want to cancel your subscription, please follow the instructions at <a
                    href="https://quiz.atrix.guide/terminate">https://quiz.atrix.guide/terminate</a>. In this case, your
                    subscription will be stopped automatically after form submission. You may send us a subscription
                    cancellation request during a **24-hour term before the end of your trial or purchased subscription
                    plans duration.** We will consider your request, after which you will receive the instructions on
                    canceling your subscription to avoid future charges. More information about subscriptions can be
                    found in the **Billing Terms.**
                </p>

                <h2 className="section-title">2. WHAT TO DO TO HAVE A REFUND</h2>
                <p className="policy-paragraph">
                    If you canceled your subscription in accordance with Section 1 of this Money-back Policy, but the
                    subscription fee was still charged to your account due to technical reasons, you may submit a refund
                    request to us that meets the following criteria:
                </p>
                <ol className="ordered-list numbered-criteria">
                    <li>you made a purchase directly on our website;</li>
                    <li>you contacted us via email **within 14 days after your initial purchase and before the end of
                        your subscription period**;
                    </li>
                    <li>In addition to a refund request and clauses a-b, please send us a written application to <a
                        href="mailto:atrix.app@support-team.app">atrix.app@support-team.app</a> containing the following
                        information:
                        <ul className="sub-list">
                            <li>The email address related to your purchase;</li>
                            <li>Payment method (PayPal or Card, etc. used to perform a purchase);</li>
                            <li>The payment date;</li>
                            <li>Payment receipt or any payment confirmation;</li>
                            <li>A brief justification of the reason for the need to a refund.</li>
                        </ul>
                    </li>
                </ol>
                <p className="policy-paragraph notice-box">
                    **PLEASE NOTE!** Only fulfilling the requirements above allows you to receive a complete refund
                    under the Money-Back Policy for your last subscription cycle. For the sake of clarity, this
                    Money-Back Policy **does not** apply to any other instances, for example, personal reasons (you
                    dislike the product, Etc.) or financial reasons (you did not expect that you would be charged, the
                    trial would convert into the subscription, the subscription would automatically renew, Etc.).
                </p>
                <p className="policy-paragraph">
                    This Money-Back policy **does not** apply to App Store and Google Play purchases or to subscription
                    renewals.
                </p>
                <p className="policy-paragraph">
                    We will review your application and notify you (by email) whether your application is approved. If
                    the application is approved, your refund will be processed, and a credit will be automatically
                    applied to your credit card or original method of payment, **typically within 30 days.**
                </p>

                <h2 className="section-title">IMPORTANT STATEMENT</h2>
                <p className="policy-paragraph">
                    Generally, if you do not meet the conditions of our Money-Back Policy set out above, the fees you
                    have paid are **non-refundable or non-exchangeable** unless otherwise stated herein or as required
                    by applicable law. Moreover, we may consider specific refund requests on a case-by-case basis and
                    grant them at our sole discretion.
                </p>

                <h2 className="section-title">Note for the EU residents:</h2>
                <p className="policy-paragraph">
                    If you are an EEA or Switzerland resident, you have the right to withdraw from the agreement for the
                    purchase of digital services without charge and without giving any reason within fourteen (14) days
                    from the date of such agreement conclusion (withdrawal period). The withdrawal right does not apply
                    if the performance of the agreement has begun with your prior express consent and your
                    acknowledgment that you thereby lose your right of withdrawal. However, if you terminate the
                    agreement during the withdrawal period, you have the right to receive a refund proportionate to the
                    provided service. When you make a purchase of a single item of digital content (such as PDF file,
                    video and so on), you expressly agree that such content is made available to you immediately. As a
                    result, you forfeit your right to withdraw and will not be eligible for a refund.
                </p>
                <p className="policy-paragraph notice-box">
                    **HEREBY EXPRESSLY CONSENT TO THE IMMEDIATE PERFORMANCE OF THE AGREEMENT AND ACKNOWLEDGE THAT YOU
                    WILL LOSE YOUR RIGHT OF WITHDRAWAL FROM THE AGREEMENT ONCE OUR SERVERS VALIDATE YOUR PURCHASE AND
                    THE APPLICABLE PURCHASE IS SUCCESSFULLY DELIVERED TO YOU.**
                </p>
                <p className="policy-paragraph">
                    Therefore, you will not be eligible for a refund, unless the digital content or services are
                    defective.
                </p>

                <h2 className="section-title">Note for residents of certain US states:</h2>
                <p className="policy-paragraph">
                    If you reside in California or Connecticut and cancel the purchase at any time before midnight of
                    the third business day after the purchase date, we will return the payment you made.
                </p>

                <h2 className="section-title">Appsella LTD</h2>
                <p className="policy-paragraph">
                    **Address:** Office 4, 6 Ioannou Karageorgiadi Mesologgioy, Agias Trias, 3032, Limassol, Cyprus<br/>
                    **Email:** <a href="mailto:atrix.app@support-team.app">atrix.app@support-team.app</a>
                </p>

            </main>
        </div>
    );
};

export default MoneyScreen;