import React, { useState, useEffect } from 'react';
import ProgressBar from "../components/progressBar";

const CityScreen = ({ onNext, onBack, currentProgress, totalScreens, updateUserData }) => {
    const [city, setCity] = useState('');
    const [country, setCountry] = useState('');
    const [isButtonVisible, setIsButtonVisible] = useState(false);
    const [isDetecting, setIsDetecting] = useState(false);

    //     IP  
    useEffect(() => {
        detectLocationByIP();
    }, []);

    //    IP
    const detectLocationByIP = async () => {
        setIsDetecting(true);
        try {
            //  ipapi.co
            const response = await fetch('https://ipapi.co/json/');

            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }

            const data = await response.json();

            //    
            if (data.city) {
                setCity(data.city);
            }
            if (data.country_name) {
                setCountry(data.country_name);
            }


        } catch (error) {
            console.error('Error detecting location:', error);
            //      fallback
            try {
                const fallbackResponse = await fetch('https://ipinfo.io/json');
                const fallbackData = await fallbackResponse.json();

                if (fallbackData.city) {
                    setCity(fallbackData.city);
                }
                if (fallbackData.country) {
                    //      
                    const countryName = await getCountryName(fallbackData.country);
                    setCountry(countryName);
                }
            } catch (fallbackError) {
                console.error('Fallback also failed:', fallbackError);
            }
        } finally {
            setIsDetecting(false);
        }
    };

    //        
    const getCountryName = async (countryCode) => {
        const countryMap = {
            'US': 'United States',
            'GB': 'United Kingdom',
            'RU': 'Russia',
            'DE': 'Germany',
            'FR': 'France',
            'IT': 'Italy',
            'ES': 'Spain',
            'CA': 'Canada',
            'AU': 'Australia',
            'JP': 'Japan',
            'CN': 'China',
            'IN': 'India',
            'BR': 'Brazil',
            //     
        };

        return countryMap[countryCode] || countryCode;
    };

    // ,     
    useEffect(() => {
        setIsButtonVisible(city.trim().length > 0);
    }, [city]);

    const handleInputChange = (e) => {
        const value = e.target.value;
        setCity(value);
    };

    const handleContinue = () => {
        if (!city.trim()) {
            return; //     
        }

        //   ", "
        let locationString = city.trim();
        if (country.trim()) {
            locationString = `${country.trim()}, ${city.trim()}`;
        }

        console.log('Location string:', locationString);
        console.log('City:', city);
        console.log('Country:', country);

        //   ", "  userData
        if (updateUserData && typeof updateUserData === 'function') {
            updateUserData({
                birthCity: locationString,
                location: locationString,
                //       
                city: city.trim(),
                country: country.trim()
            });
            console.log('Saved to userData:', locationString);
        }

        //    
        if (onNext && typeof onNext === 'function') {
            setTimeout(() => {
                onNext();
            }, 300);
        }
    };

    //   Enter
    const handleKeyPress = (e) => {
        if (e.key === 'Enter' && city.trim()) {
            handleContinue();
        }
    };

    return (
        <div className="App animate__animated animate__fadeIn">
            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text">{currentProgress}/{totalScreens}</div>
            </header>

            <ProgressBar currentStep={currentProgress} totalSteps={totalScreens} />

            <main style={{overflow: "hidden"}}>
                <p className="title mt-24 font-size-24 philosopher-bold">
                    Where were you born?
                </p>
                <p className="sub-heading">
                    The place is important to explore your core personality traits, needs, and desires
                </p>

                <div className="input-container">
                    <input
                        className="city-input"
                        type="text"
                        value={country+', '+city}
                        onChange={handleInputChange}
                        onKeyPress={handleKeyPress}
                        placeholder="Your birthplace"
                        autoFocus
                    />
                </div>
            </main>

            <div className="button-container">
                {isButtonVisible && (
                    <button
                        className="next-btn animate__animated animate__fadeIn"
                        onClick={handleContinue}
                        style={{
                            opacity: isButtonVisible ? 1 : 0,
                            transform: isButtonVisible ? 'translateY(0)' : 'translateY(20px)',
                            transition: 'opacity 0.3s, transform 0.3s'
                        }}
                    >
                        Continue
                    </button>
                )}
            </div>
        </div>
    );
};

export default CityScreen;