import React, { useState, useEffect } from 'react';
import ProgressBar from '../components/progressBar.js';
import { getAstroData, getZodiacIcon } from '../components/zodiacutils.js';

const AstroMapScreen = ({ onNext, onBack, currentProgress, totalScreens, userData }) => {
    const [astroData, setAstroData] = useState({
        moonSign: 'Cancer',
        sunSign: 'Aquarius',
        ascendant: 'Libra'
    });

    //       
    useEffect(() => {
        if (userData?.birthdayString) {
            console.log('Calculating astrology data for:', userData.birthdayString);
            const calculatedData = getAstroData(userData.birthdayString);
            console.log('Astro data:', calculatedData);
            setAstroData(calculatedData);
        }
    }, [userData?.birthdayString]);

    return (
        <div className="App">
            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text">{currentProgress}/{totalScreens}</div>
            </header>

            <ProgressBar currentStep={currentProgress} totalSteps={totalScreens} />

            <main>
                <div className="message animate__animated animate__fadeIn" style={{ animationDelay: '7s' }}>
                    <div className="message-text">
                        Your chart shows a <span className="blue-text">rare spark</span> - let's uncover how you can use this power!
                    </div>
                </div>

                <p className="sub-heading animate__animated animate__fadeIn" style={{ animationDelay: '7s' }}>
                    <img src="/women.webp" alt="" className="message-avatar"/>
                </p>

                <div className="astromap-container">
                    <img className="astromap-bg" src="/assets/img/astromap-background.svg" alt=""/>
                    <div className="floating-messages-container">
                        <div className="floating-message">
                            <div className="floating-text">Your intellectual side</div>
                        </div>
                        <div className="floating-message">
                            <div className="floating-text">Your approach to love</div>
                        </div>
                        <div className="floating-message">
                            <div className="floating-text">Your strengths</div>
                        </div>
                        <div className="floating-message">
                            <div className="floating-text">Your changes</div>
                        </div>
                    </div>
                    <div className="background-pulse"></div>
                    <img className="astromap" src="/assets/img/astromap.svg" alt="Astro Map"/>
                </div>

                <div className="zodiacs animate__animated animate__fadeIn" style={{ animationDelay: '7s' }}>
                    <div className="zodiac-items">
                        <img
                            src={getZodiacIcon(astroData.moonSign)}
                            alt={astroData.moonSign}
                        />
                        <p className="title">{astroData.moonSign}</p>
                        <p className="sub-title">Moon Sign</p>
                    </div>
                    <div className="zodiac-items">
                        <img
                            src={getZodiacIcon(astroData.sunSign)}
                            alt={astroData.sunSign}
                        />
                        <p className="title">{astroData.sunSign}</p>
                        <p className="sub-title">Sun Sign</p>
                    </div>
                    <div className="zodiac-items">
                        <img
                            src={getZodiacIcon(astroData.ascendant)}
                            alt={astroData.ascendant}
                        />
                        <p className="title">{astroData.ascendant}</p>
                        <p className="sub-title">Ascendant</p>
                    </div>
                </div>

            </main>

            <div className="button-container">
                <button
                    className="next-btn animate__animated animate__fadeIn"
                    style={{ animationDelay: '7s' }}
                    onClick={onNext}
                >
                    Continue
                </button>
            </div>
        </div>
    );
};

export default AstroMapScreen;