import React, { useState, useEffect } from 'react';
import ProgressBar from "../components/progressBar";
import {
    getAstroData,
    getZodiacIcon,      //    ()
    getZodiacPackIcon,  //    ()
    getZodiacElement,
    getZodiacModality,
    getZodiacPolarity,
    getModalityIcon,    //    zodiacicons
    getPolarityIcon,    //    zodiacpack
    formatBirthDate
} from '../components/zodiacutils';

const PeopleCardScreen = ({ onNext, onBack, currentProgress, totalScreens, userData }) => {
    const [astroData, setAstroData] = useState({
        moonSign: 'Cancer',
        sunSign: 'Aquarius',
        ascendant: 'Libra'
    });
    const [zodiacDetails, setZodiacDetails] = useState({
        element: 'Earth',
        modality: 'Cardinal',
        polarity: 'Feminine',
        sunSignIcon: '/assets/img/zodiacpack/oven.svg'
    });

    //      
    useEffect(() => {
        if (userData?.birthdayString) {
            console.log(' PeopleCard: Calculating for:', userData.birthdayString);

            //   
            const calculatedAstroData = getAstroData(userData.birthdayString);
            setAstroData(calculatedAstroData);

            //      
            const sunSign = calculatedAstroData.sunSign;
            const element = getZodiacElement(sunSign);
            const modality = getZodiacModality(sunSign);
            const polarity = getZodiacPolarity(sunSign);

            // :  getZodiacPackIcon     !
            const sunSignIcon = getZodiacPackIcon(sunSign);

            //  
            console.log(' ICON PATHS DEBUG:');
            console.log('  Sun Sign:', sunSign);
            console.log('  Small icon (zodiacicons):', getZodiacIcon(sunSign));
            console.log('  Big icon (zodiacpack):', sunSignIcon);
            console.log('  Is big icon from zodiacpack?', sunSignIcon.includes('zodiacpack'));
            console.log('  Is small icon from zodiacicons?', getZodiacIcon(sunSign).includes('zodiacicons'));

            setZodiacDetails({
                element,
                modality,
                polarity,
                sunSignIcon //    zodiacpack
            });
        }
    }, [userData?.birthdayString]);

    // ,    
    console.log(' Import check:', {
        getZodiacPackIcon: typeof getZodiacPackIcon,
        getZodiacIcon: typeof getZodiacIcon,
        testPackIcon: getZodiacPackIcon ? getZodiacPackIcon('Aries') : 'NOT LOADED',
        testSmallIcon: getZodiacIcon ? getZodiacIcon('Aries') : 'NOT LOADED'
    });

    //   
    const getFormattedBirthDate = () => {
        return formatBirthDate(userData?.birthdayString);
    };

    //    
    const getGoalsText = () => {
        if (!userData?.goals || userData.goals.length === 0) return 'Friends';
        return userData.goals.slice(0, 2).map(goal => goal.text).join(', ');
    };

    //  
    const getCityText = () => {
        if (!userData?.birthCity) return 'Moscow';
        return userData.birthCity;
    };

    //   
    const getRelationshipText = () => {
        if (!userData?.relationshipStatusText) return 'Engaged';
        return userData.relationshipStatusText;
    };

    //    
    const getGenderText = () => {
        if (!userData?.gender) return 'Man';
        return userData.gender === 'male' ? 'Man' :
            userData.gender === 'female' ? 'Woman' : 'Person';
    };

    return (
        <div className="App animate__animated animate__fadeIn">
            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text">{currentProgress}/{totalScreens}</div>
            </header>

            <ProgressBar currentStep={currentProgress} totalSteps={totalScreens} />

            <main>
                <div className="message animate__animated animate__fadeIn" style={{animationDelay: '0.5s'}}>
                    <div className="message-text">
                        Your chart shows a <span className="blue-text">rare spark</span> - let's uncover how you can
                        use this power!
                    </div>
                </div>

                <p className="sub-heading animate__animated animate__fadeIn" style={{animationDelay: '0.5s'}}>
                    <img src="/women.webp" alt="" className="message-avatar"/>
                </p>

                <div className="people-card">
                    <h2 className="people-card-title philosopher-regular animate__animated animate__fadeIn"
                        style={{animationDelay: '1.5s'}}>You</h2>

                    <div className="sub-heading no-margin animate__animated animate__fadeIn"
                         style={{animationDelay: '2s'}}>
                        {getGenderText()}  {astroData.sunSign}  {zodiacDetails.element}
                    </div>

                    <div className="people-card-img-container">
                        {/*   -   zodiacpack () */}
                        <div className="zodiac-items animate__animated animate__fadeInLeft"
                             style={{animationDelay: '3s'}}>
                            <img style={{width:'32px'}}
                                 src={getModalityIcon(zodiacDetails.modality)}
                                 alt={zodiacDetails.modality}
                                 onError={(e) => {
                                     console.error('Failed to load modality icon:', getModalityIcon(zodiacDetails.modality));
                                     e.target.src = '/assets/img/zodiacpack/cardinal.svg';
                                 }}/>
                            <p className="title">{zodiacDetails.modality}</p>
                            <p className="sub-title">Modality</p>
                        </div>

                        {/*   -    zodiacpack */}
                        <div className="people-card-items animate__animated animate__fadeIn"
                             style={{animationDelay: '2.5s'}}>
                            <img className="people-card-item-z1"
                                 src="/assets/img/cardlogobg.png"
                                 alt="Card Background"/>
                            {/* :    getZodiacPackIcon() */}
                            <img className="people-card-item-z2"
                                 src={zodiacDetails.sunSignIcon}
                                 alt={astroData.sunSign}
                                 onError={(e) => {
                                     console.error('Failed to load zodiacpack icon:', zodiacDetails.sunSignIcon);
                                     console.log('Trying fallback...');
                                     //   
                                     const fallbackIcon = `/assets/img/zodiacpack/${astroData.sunSign.toLowerCase()}.svg`;
                                     e.target.src = fallbackIcon;
                                     console.log('Fallback path:', fallbackIcon);
                                 }}/>
                        </div>

                        {/*   -   zodiacpack () */}
                        <div className="zodiac-items animate__animated animate__fadeInRight"
                             style={{animationDelay: '3s'}}>
                            <img style={{width:'32px'}}
                                 src={getPolarityIcon(zodiacDetails.polarity)}
                                 alt={zodiacDetails.polarity}
                                 onError={(e) => {
                                     console.error('Failed to load polarity icon:', getPolarityIcon(zodiacDetails.polarity));
                                     e.target.src = '/assets/img/zodiacpack/feminine.svg';
                                 }}/>
                            <p className="title">{zodiacDetails.polarity}</p>
                            <p className="sub-title">Polarity</p>
                        </div>
                    </div>

                    <div className="animation-sequence animate__animated animate__fadeIn" style={{animationDelay: '5s'}}>
                        <div className="reliable-container">
                            <div className="reliable-title">Details</div>
                            <div className="reliable-list">
                                <div className="reliable-item">
                                    <span>Birth</span>
                                    <span>{getFormattedBirthDate()}</span>
                                </div>
                                <div className="reliable-item">
                                    <span>Place</span>
                                    <span>{getCityText()}</span>
                                </div>
                                <div className="reliable-item">
                                    <span>Status</span>
                                    <span>{getRelationshipText()}</span>
                                </div>
                                <div className="reliable-item">
                                    <span>Goals</span>
                                    <span>{getGoalsText()}</span>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/*  -    zodiacicons */}
                    <div className="card-footer">
                        <div className="zodiac-items animate__animated animate__fadeIn"
                             style={{animationDelay: '12.5s'}}>
                            <img style={{width:'32px'}}
                                 src={getZodiacIcon(astroData.moonSign)} //   zodiacicons
                                 alt={astroData.moonSign}
                                 onError={(e) => {
                                     console.error('Failed to load moon icon:', getZodiacIcon(astroData.moonSign));
                                     e.target.src = '/assets/img/zodiacicons/rak.svg';
                                 }}/>
                            <p className="title">{astroData.moonSign}</p>
                            <p className="sub-title">Moon Sign</p>
                        </div>
                        <div className="zodiac-items animate__animated animate__fadeIn"
                             style={{animationDelay: '13.5s'}}>
                            <img style={{width:'32px'}}
                                 src={getZodiacIcon(astroData.sunSign)} //   zodiacicons
                                 alt={astroData.sunSign}
                                 onError={(e) => {
                                     console.error('Failed to load sun icon:', getZodiacIcon(astroData.sunSign));
                                     e.target.src = '/assets/img/zodiacicons/oven.svg';
                                 }}/>
                            <p className="title">{astroData.sunSign}</p>
                            <p className="sub-title">Sun Sign</p>
                        </div>
                        <div className="zodiac-items animate__animated animate__fadeIn"
                             style={{animationDelay: '14.5s'}}>
                            <img style={{width:'32px'}}
                                 src={getZodiacIcon(astroData.ascendant)} //   zodiacicons
                                 alt={astroData.ascendant}
                                 onError={(e) => {
                                     console.error('Failed to load ascendant icon:', getZodiacIcon(astroData.ascendant));
                                     e.target.src = '/assets/img/zodiacicons/vesi.svg';
                                 }}/>
                            <p className="title">{astroData.ascendant}</p>
                            <p className="sub-title">Ascendant</p>
                        </div>
                    </div>
                </div>
            </main>

            <div className="button-container">
                <button className="next-btn animate__animated animate__fadeIn"
                        style={{ animationDelay: '9.5s' }}
                        onClick={onNext}>
                    Continue
                </button>
            </div>
        </div>
    );
};

export default PeopleCardScreen;