import React, { useState, useEffect, useRef } from 'react';
import ProgressBar from "../components/progressBar";

const BirthdayScreen = ({onBack, currentProgress, totalScreens, onNext, updateUserData }) => {
    const [selectedDate, setSelectedDate] = useState({
        month: 'April',
        day: '1',
        year: '2000'
    });

    const monthRef = useRef(null);
    const dayRef = useRef(null);
    const yearRef = useRef(null);

    const months = ['January', 'February', 'March', 'April', 'May', 'June',
        'July', 'August', 'September', 'October', 'November', 'December'];
    const days = Array.from({ length: 31 }, (_, i) => (i + 1).toString());
    const years = Array.from({ length: 61 }, (_, i) => (1950 + i).toString());

    //  
    useEffect(() => {
        const updateScrollPosition = (pickerRef, value, items) => {
            const index = items.indexOf(value);
            if (index >= 0 && pickerRef.current) {
                pickerRef.current.scrollTop = (index + 2) * 50;
            }
        };

        updateScrollPosition(monthRef, selectedDate.month, months);
        updateScrollPosition(dayRef, selectedDate.day, days);
        updateScrollPosition(yearRef, selectedDate.year, years);

        //   
        setTimeout(() => {
            if (monthRef.current) handleScroll(monthRef, 'month');
            if (dayRef.current) handleScroll(dayRef, 'day');
            if (yearRef.current) handleScroll(yearRef, 'year');
        }, 100);
    }, []);

    //  
    const handleScroll = (pickerRef, type) => {
        const picker = pickerRef.current;
        if (!picker) return;

        const options = picker.querySelectorAll('.picker-option');
        const pickerRect = picker.getBoundingClientRect();
        const centerY = pickerRect.top + pickerRect.height / 2;

        let selectedValue = selectedDate[type];

        options.forEach(option => {
            const optionRect = option.getBoundingClientRect();
            const optionCenterY = optionRect.top + optionRect.height / 2;
            const distance = Math.abs(centerY - optionCenterY);

            if (distance < 25 && option.textContent) {
                option.classList.add('active');
                selectedValue = option.textContent;
            } else {
                option.classList.remove('active');
            }
        });

        if (selectedValue !== selectedDate[type]) {
            setSelectedDate(prev => ({
                ...prev,
                [type]: selectedValue
            }));
        }
    };

    //   
    useEffect(() => {
        const handlePickerScroll = () => {
            if (monthRef.current) handleScroll(monthRef, 'month');
            if (dayRef.current) handleScroll(dayRef, 'day');
            if (yearRef.current) handleScroll(yearRef, 'year');
        };

        const monthPicker = monthRef.current;
        const dayPicker = dayRef.current;
        const yearPicker = yearRef.current;

        [monthPicker, dayPicker, yearPicker].forEach(picker => {
            picker?.addEventListener('scroll', handlePickerScroll);
        });

        return () => {
            [monthPicker, dayPicker, yearPicker].forEach(picker => {
                picker?.removeEventListener('scroll', handlePickerScroll);
            });
        };
    }, [selectedDate]);

    const handleContinue = () => {
        console.log(' Continue button clicked');

        const birthdayString = `${selectedDate.month} ${selectedDate.day}, ${selectedDate.year}`;
        const birthdayDate = new Date(`${selectedDate.month} ${selectedDate.day}, ${selectedDate.year}`);

        console.log('Selected birthday:', birthdayString);

        //  
        if (updateUserData && typeof updateUserData === 'function') {
            updateUserData({
                birthday: birthdayDate,
                birthdayString: birthdayString
            });
            console.log(' Birthday saved');
        }

        //     (birthtime)
        if (onNext && typeof onNext === 'function') {
            console.log(' Calling onNext to navigate to birthtime');
            setTimeout(() => {
                onNext();
            }, 300);
        } else {
            console.error(' onNext is not available or not a function');
        }
    };

    const renderPickerOptions = (items) => {
        return [
            ...Array(2).fill(null).map((_, i) =>
                <div key={`empty-top-${i}`} className="picker-option"></div>
            ),
            ...items.map((item, index) => (
                <div
                    key={index}
                    className="picker-option"
                >
                    {item}
                </div>
            )),
            ...Array(2).fill(null).map((_, i) =>
                <div key={`empty-bottom-${i}`} className="picker-option"></div>
            )
        ];
    };

    return (
        <div className="App animate__animated animate__fadeIn">
            <header>
                <button className="back-arrow" onClick={onBack}></button>
                <div className="title">Atrix</div>
                <div className="progress-text">{currentProgress}/{totalScreens}</div>
            </header>

            <ProgressBar currentStep={currentProgress} totalSteps={totalScreens} />

            <main>
                <div className="birthday-picker-container">
                    <div className="container">
                        <h1 className="title">When's your birthday?</h1>
                        <p className="subtitle">
                            It's also important to know your date of birth<br />
                            for making complete and accurate predictions
                        </p>

                        <div className="picker-container">
                            <div className="picker-highlight"></div>

                            <div className="picker-column">
                                <div className="picker-label">Month</div>
                                <div className="picker-wrapper" ref={monthRef}>
                                    {renderPickerOptions(months)}
                                </div>
                            </div>

                            <div className="picker-column">
                                <div className="picker-label">Day</div>
                                <div className="picker-wrapper" ref={dayRef}>
                                    {renderPickerOptions(days)}
                                </div>
                            </div>

                            <div className="picker-column">
                                <div className="picker-label">Year</div>
                                <div className="picker-wrapper" ref={yearRef}>
                                    {renderPickerOptions(years)}
                                </div>
                            </div>
                        </div>


                    </div>
                </div>
            </main>
            <div className="button-container">
                <button className="next-btn animate__animated animate__fadeIn" style={{ animationDelay: '3s' }} onClick={handleContinue}>
                    Continue
                </button>
            </div>
        </div>
    );
};

export default BirthdayScreen;